
.widget-area {  
	padding: 15px;
	margin-bottom: 1rem;
	margin-top: 1rem;
	ul{
		list-style: none;
	}
	li{
		padding: 8px 0px;
		border-bottom: solid 1px rgb(238, 238, 238);
	}
	a{
		color: #636B61!important;
		&:hover{
			text-decoration: none;
			color: #E9B44C !important;
		}
		
	}
	
}

#secondary {
	ul {
		margin-left: 20px;
		padding-left: 0;
		li {
			padding: 5px 0;
		}
	}
	.widget-title {
		border-bottom: solid 1px #32021F;
		box-shadow: 0 1px darken(#32021F, 12%);
		padding-bottom: 10px;
		
	}
}
//    adding styles to sidebar headings 
h2{
	font-size: 1.5rem;
}
 
//  styling inner side of the sidebar
.widget{
	background:#ffff;
	padding: 1rem;
}